<template>
  <div class="header-component" :class="{'shown': !hidden, 'change': changeTopBar}">
    <div class="logo-menu">
      <div class="logo-block">
        <a class="logo"><img src="../../assets/images/logo.png" alt=""></a>
        <div class="contact">
          1-855-900-BeMo
        </div>
        <div v-if="!hidden" class="close" @click="hidden = !hidden">
          <img src="../../assets/images/close.png" alt="">
        </div>
        <div v-if="hidden" class="menu-dots" @click="hidden = !hidden">
          <img src="../../assets/images/menu-dots.png" alt="">
        </div>
      </div>
      <menu-component :class="{'shown': !hidden}"/>
    </div>
    <div class="login-contact" :class="{'hidden': hidden}">
      <div class="contact">
        1-855-900-BeMo
      </div>
      <a v-if="changeButtons" href="#path" v-smooth-scroll="{ duration: 3000, offset: -300}">
        <div class="button button-blue" @click="hidden=true">Choose Your Path</div>
      </a>
      <a v-if="!changeButtons" href="">
        <div class="button button-blue" @click="hidden=true">Login</div>
      </a>
      <div class="button button-white">{{changeButtons ? 'Free Strategy Call' : 'Contact Us'}}</div>
    </div>
  </div>
</template>

<script>
    import MenuItem from "../menu/components/menu-item";
    import MenuComponent from "../menu/index";

    export default {
        name: "headerComponent",
        components: {MenuComponent, MenuItem},
        data() {
            return {
                hidden: true,
                changeButtons: false,
                changeTopBar: false
            }
        },
        methods: {
            handleScroll() {
                this.changeButtons = window.innerHeight <= window.pageYOffset;
                this.changeTopBar = window.pageYOffset > 0  && window.innerWidth;
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        }
    }
</script>

<style scoped>

</style>



