<template>
  <div class="menu-component">
    <menu-item v-for="(item, index) in menuItems" :item="item" :key="'menu'+index"/>
  </div>
</template>

<script>

    import MenuItem from "./components/menu-item";
    export default {
        name: "menu-component",
        components: {MenuItem},
        data() {
            return {
            menuItems: [
                    {
                        name: 'Field of Study'
                    },
                    {
                        name: 'Services',
                        sub: [
                            'Medicine',
                            'Residency',
                            'Other Programs',
                            'Parents'
                        ]
                    },
                    {
                        name: 'Resources'
                    },
                    {
                        name: 'Reviews'
                    },
                    {
                        name: 'About'
                    }
                ]
            }
        }
    }
</script>

<style scoped>

</style>



