<template>
    <div class="proven-results">
      <div class="text-part">
        <h2>Proven results and
          real human support</h2>
        <div class="text">
          Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et
          dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita
          kasd gubergren, no sea takimata sanctus est
        </div>
        <div class="button button-white">Learn More</div>
      </div>
      <div class="diagram-part" ref="diagram">
        <div class="column">
          <div class="percents">91%</div>
          <div class="value" :class="{'referral': referral}"></div>
          <div class="diagram-name">Referral rate</div>
        </div>
        <div class="column">
          <div class="percents">93.5%</div>
          <div class="value" :class="{'success': success}"></div>
          <div class="diagram-name">Success rate</div>
        </div>
        <div class="column">
          <div class="percents">99%</div>
          <div class="value" :class="{'satisfaction': satisfaction}"></div>
          <div class="diagram-name">Satisfaction rate</div>
        </div>
        <div class="column">
          <div class="percents">100%</div>
          <div class="value" :class="{'pure': pure}"></div>
          <div class="diagram-name">Pure love</div>
        </div>
      </div>
      <div class="button button-white mobile">Learn More</div>
    </div>
</template>

<script>
    export default {
        name: "provenResults",
        data() {
            return {
                referral: false,
                success: false,
                satisfaction: false,
                pure: false
            }
        },
        methods: {
            handleScroll() {
                let diagramPosition = window.pageYOffset + this.$refs.diagram.getBoundingClientRect().bottom
                let windowPosition = window.pageYOffset + window.innerHeight

                if (diagramPosition < windowPosition) {
                    this.referral= true,
                        this.success= true,
                        this.satisfaction= true,
                        this.pure= true
                }
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
        }
    }
</script>

<style scoped>

</style>



