<template>
  <div class="menu-item"
       :class="{'with-sub': item.sub}"
       @mouseover="item.sub ? subMenu=true : ''"
       @mouseleave="item.sub ? subMenu=false : ''">
    <div class="item-name">
      {{item.name}}
      <img :class="{'rotate': subMenu}" v-if="item.sub" src="../../../assets/images/drop-down-arrow.png" alt="dropdown">
    </div>
    <div :class="{'visible': subMenu}" class="sub-menu">
      <div class="sub-menu-items">
        <div class="sub-menu-item" v-for="(subItem, index) in item.sub" :key="'sub' + index">
          {{subItem}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "menu-item",
        props: ['item'],
        data() {
            return {
                subMenu: false
            }
        }
        ,
    }
</script>

<style scoped>

</style>



