<template>
  <div class="get-into-dream">
    <h3>Get into your dream
      program with BeMo.
      Guaranteed!
    </h3>
    <div class="text">
      Scientific admissions prep. Bold dreams.
    </div>
    <div class="buttons mobile">
      <div class="button button-round"><img src="../../assets/images/play.png" alt="play"></div>
      Watch the film
    </div>
    <div ref="buttons" class="buttons" :class="{'to-fixed': toFixed}" :style="{'top': (headerHeight - 1) + 'px'}">
<!--      <a @click="scrollTo" href="#path" class="button button-blue">Choose Your Path</a>-->
      <a href="#path" v-smooth-scroll="{ duration: 3000, offset: -300}" class="button button-blue">Choose Your Path</a>
      <div class="button button-white">Free Strategy Call</div>
      <div class="button button-round"><img src="../../assets/images/play.png" alt="play"></div>
      <span>Watch the film</span>
    </div>
  </div>
</template>

<script>
    export default {
        name: "getIntoDream",
        props: ['headerHeight'],
        data() {
            return {
                toFixed: false,
                curPos: 0
            }
        },
        methods: {
            handleScroll() {
                let buttonPosition = this.$refs.buttons.getBoundingClientRect().top

                if (buttonPosition <= this.headerHeight) {
                    this.toFixed = true
                }if (window.scrollY <= this.curPos - this.headerHeight) {
                    this.toFixed = false
                }
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            this.curPos = this.$refs.buttons.getBoundingClientRect().top
        }
    }
</script>

<style scoped>

</style>



