<template>
  <div class="main-page">
    <div ref="header">
      <header-component/>
    </div>
    <get-into-dream ref="get-into" :header-height="headerHeight"/>
    <comments/>
    <div class="at-position" ref="path" :class="{'down': path}">
      <choose-path/>
    </div>
    <div class="at-position" ref="believe" :class="{'down': believe}">
      <we-believe/>
    </div>
    <div class="at-position" ref="results" :class="{'down': results}">
      <proven-results/>
    </div>
    <div class="at-position" ref="experts" :class="{'down': experts}">
      <experts/>
    </div>
    <div class="at-position" ref="dreams" :class="{'down': dreams}">
      <turn-dreams/>
    </div>
    <div class="at-position" ref="guarantees" :class="{'down': guarantees}">
      <guarantees/>
    </div>
    <div class="at-position" ref="take" :class="{'down': take}">
      <just-take/>
    </div>
    <div class="at-position" ref="seen" :class="{'down': seen}">
      <as-seen/>
    </div>
    <footer-component/>
  </div>
</template>

<script>
    import HeaderComponent from "../components/header/index";
    import GetIntoDream from "../components/getIntoDream/index";
    import Comments from "../components/comments/index";
    import ChoosePath from "../components/choosePath/index";
    import WeBelieve from "../components/weBelive/index";
    import ProvenResults from "../components/provenResults/index";
    import TurnDreams from "../components/turnDreams/index";
    import Guarantees from "../components/guarantees/guarantees";
    import JustTake from "../components/justTake/index";
    import AsSeen from "../components/asSeen/index";
    import FooterComponent from "../components/footer/index";
    import Experts from "../components/experts/index";

    export default {
        name: "mainPage",
        components: {
            Experts,
            FooterComponent,
            AsSeen,
            JustTake,
            Guarantees,
            TurnDreams,
            ProvenResults,
            WeBelieve,
            ChoosePath,
            Comments,
            GetIntoDream,
            HeaderComponent
        },
        data() {
            return {
                path: true,
                believe: true,
                results: true,
                experts: true,
                dreams: true,
                guarantees: true,
                take: true,
                seen: true,
                headerHeight: 0
            }
        },
        methods: {
            handleScroll() {
                let pathPosition = this.$refs.path.getBoundingClientRect().top
                let believePosition = this.$refs.believe.getBoundingClientRect().top
                let resultsPosition = this.$refs.results.getBoundingClientRect().top
                let expertsPosition = this.$refs.experts.getBoundingClientRect().top
                let dreamsPosition = this.$refs.dreams.getBoundingClientRect().top
                let guaranteesPosition = this.$refs.guarantees.getBoundingClientRect().top
                let takePosition = this.$refs.take.getBoundingClientRect().top
                let seenPosition = this.$refs.seen.getBoundingClientRect().top
                let windowPosition = window.innerHeight

                if (pathPosition < windowPosition) {
                    this.path = false
                }
                if (believePosition < windowPosition) {
                    this.believe = false
                }
                if (resultsPosition < windowPosition) {
                    this.results = false
                }
                if (expertsPosition < windowPosition) {
                    this.experts = false
                }
                if (dreamsPosition < windowPosition) {
                    this.dreams = false
                }
                if (guaranteesPosition < windowPosition) {
                    this.guarantees = false
                }
                if (takePosition < windowPosition) {
                    this.take = false
                }
                if (seenPosition < windowPosition) {
                    this.seen = false
                }
            }
        },
        mounted() {
            window.addEventListener('scroll', this.handleScroll);
            this.headerHeight = this.$refs.header.clientHeight
        }
    }
</script>

<style scoped>

</style>



