<template>
  <div id="path" class="choose-path">
    <h2>Choose Your Path</h2>
    <div class="path-list">
      <div class="path" v-for="(item, index) in paths" :key="'path' + index">
          {{item}}
      </div>
    </div>
  </div>
</template>

<script>

    export default {
        name: "choosePath",
        data() {
            return {
                paths: [
                    'Medicine',
                    'Residency',
                    'Physician Assistant',
                    'Dentistry',
                    'Pharmacy',
                    'Nursing',
                    'Lorem ipsum',
                    'Law',
                    'Graduate',
                    'Other'
                ],
                path: 'Medicine',
            }
        }
    }
</script>

<style scoped>

</style>



